import React,{useEffect, useRef,useState} from 'react';
import '../SCSS/Header.css';
import Logo from '../img/action.png';
import {Col} from 'react-bootstrap';
import {NavLink} from 'react-router-dom';
import Menu from './HeaderMenu/Menu';
//import HomePage from '../SubComponents/HomePage';

const Header = () => {
   
    const headerRef = useRef(null);
    const [sticky,setSticky] = useState(false);
    

    const styleStickyHeader = {
        height:'80px',
        position:'fixed',
        zIndex:'300',
        background:'white',
        display:'flex',
        flexDirection:'row'
    }

    const headerSticky =()=>{
        const topWindow = window.pageYOffset;
        const heightHeader = headerRef.current.offsetHeight;

        heightHeader < topWindow  ? setSticky(true) : setSticky(false)
    }

    useEffect(()=>{
        window.addEventListener('scroll',headerSticky);
    })

    return ( 
        <>
            <Col className='header-col' style={sticky === true ? styleStickyHeader : null} ref={headerRef}>
                <div className='header-contents'>
                    <NavLink to='/'><img src={Logo} alt='' style={sticky === true ? {width:'8rem'} : null} /></NavLink>
                </div>
                <div className='header-contents'>
                    <Menu stickyMenu={sticky}/>
                </div>
            </Col>
        </>
     );
}
 
export default Header;