import React,{useState,useEffect} from 'react';

const RenderPropsToggler = (props) => {

    const [widthForToggler, setwidthForToggler] = useState(false);
    
    const widthPage =()=>{
        const widthPage = window.innerWidth;
        widthPage <= 768 ? setwidthForToggler(true) : setwidthForToggler(false);
    }

    useEffect(()=>{
        window.addEventListener('resize',widthPage);
    })

    return ( 
        <>
            {props.render(widthForToggler,props.stickyMenu)}
        </>
     );
}
 
export default RenderPropsToggler;