import React from 'react';
import {NavLink} from 'react-router-dom'
import Toggler from './Toggler';
import RenderPropsToggler from './RenderPropsToggler';
import {useSelector} from 'react-redux';

const Menu = (props) => {
    const menu = useSelector(state => state.mainReducer.isMenu);
    const {stickyMenu} = props;

    const styleMenuSticky = {
        position:'absolute',
        width:'100vw',
        left:'-100%'
    }

    return ( 
        <>
            <div className='menu'>
                <RenderPropsToggler render={(width,stickyMenu)=> <Toggler width={width} stickyMenu={props.stickyMenu} />}/>
                {menu === true ? <div className='menu-items' style={stickyMenu === true ? styleMenuSticky  : null}>
                    <NavLink to="/creator">KREATOR</NavLink>
                    <NavLink to="/aboutus">O NAS</NavLink>
                    <NavLink to="/contact">KONTAKT</NavLink>
                </div> : null}
            </div>
        </>
     );
}
 
export default Menu;


