import React from 'react';
import {useDispatch,useSelector} from 'react-redux';

const Toggler = (props) => {
    const dispatch = useDispatch();
    const menuDropped = useSelector(state => state.mainReducer.isMenu);
    const {width,stickyMenu} = props;
    const styleToggler ={
        position: 'absolute',
        right: '20%',
        top:'0'
    }

    const changeToggler =()=>{
        dispatch({type:'MENU'})
    }

    return ( 
        <>
            <button className='toggler' onClick={changeToggler} style={width === true && stickyMenu === true ? styleToggler : null}>
                {menuDropped === false ?
                <div className='toggler-hamburger'>
                    <span></span>
                    <span></span>
                    <span></span>
                </div> 
                :
                <div className='toggler-double'>
                    <span></span>
                    <span></span>
                </div>  
                }
            </button>
        </>
     );
}
 
export default Toggler;